lex-start;\n    > * {\n      display: inline-block;\n    }\n    & + &   { margin-left: 1rem }\n    > * + * { margin-top:  1rem }\n\n    @include Break( min-width Breaks(3) ){\n      flex: 1 0 auto;\n      &:last-child {\n        flex: 1 0 50%;\n      }\n    }\n  }\n\n  @include Break( max-width Breaks(3) ){\n    &, &--group {\n      display: contents;\n    }\n    &--group {\n      & + & >:first-child { margin-top: 1rem }\n      & + & >:last-child  { margin-top:.5rem }\n    }\n  }\n  & {\n    // load case\n    background-repeat: no-repeat;\n    background-position: center;\n    background-size: 2.3em 2.3em;\n    &, > * { transition: all .2s ease }\n    &_loading {\n      position: relative;\n      margin-top: -3rem;\n      background-image: url(\"~@/assets/preloader.gif\");\n      background-color: Color(light);\n      opacity: .5;\n      & > * { opacity: 0 !important }\n    }\n  }\n}\n\n.SubmitStatus {\n  .ActionBox:first-child + .ContactForm:last-child > & {\n    flex: 0 0 100%;\n    min-width: 100%;\n    position: relative;\n    z-index: 1;\n    margin-top: -4.3rem;\n    background: Color(light);\n  }\n  /deep/ &_error .UiHeading {\n    color: Color(brand);\n    font-weight: 900;\n  }\n  &_success .UiHeading {\n    color: Color(theme);\n    font-weight: 100;\n  }\n  /deep/ a {\n    font-weight: bold;\n  }\n}\n","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-brand: #D21034;\n$c-alt:   #7D1214;\n\n$colors:(\n  dark:      $c-black, // #0E0E0E\n  slate:     lighten($c-black,15%), // #323232,\n  silver:    lighten($c-black,67%), // #B8B8B8\n  cream:     lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  creamDark: mix(#BFB4A0,$c-black,75%),\n  light:     mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:     $c-matte, // #F0F0F0\n  theme:     $c-theme, // #BA9454\n  alt:       $c-alt,\n  brand:     $c-brand,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}","/////////////////////////